	import service from "../../service.js";
	import headers from "../../components/header_nav.vue"
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore,
			headers
		},
		data() {
			return {
				Htitle: "佣金审核",
				Hlicon: "../../static/icon/left_whitch.png",
				merchid:uni.getStorageSync("merchid"),
				index:1,
				navlist:[
					{
						id:1,
						title:"审核中"
					},
					{
						id:2,
						title:"已通过"
					}
				],
				comlist:[],//佣金数据列表
					/* {
					title:'申达股份出错',
					total_commision:'43',
					create_time:'2020-10-10 00:00:00',
					status:1,
				} */
				isLoading:false,
				scroll:false,
				status:0,
				pages:1,
				page:0,
				out:0,
			}
		},
		onShow:function(){
			this.loadHistory();
		},
		onReachBottom() {
			this.loadHistory();
		},
        methods: {
			//加载历史数据
			loadHistory(e){
				if(this.isLoading){
					return ;
				}
				if(this.page < this.pages){
					this.out = 0;
					this.isLoading = true;
					this.scroll = false;
					this.status = 1;
					var page = this.page+1;
					service.auth(this, service.api.service.auditcom, 'GET', {
						'page':page,
						'merch_id':this.merchid,
						'status': this.index
					}, function(self, res) {
						if (res.code == 1) {
							var list = res.data.data;
							for(let i=0;i<list.length;i++){
								//将数据逐条写入数组
								self.comlist.push(list[i]);
							}
							self.pages = res.data.page_total;
							self.page = page;
							self.scroll = true;
							self.isLoading = false;
							/* setTimeout(function(){ */
								self.out = 1;
							/* },1000); */
							uni.setStorageSync('tasktime',1);
							if(self.page == self.pages || res.data.total == 0){
								self.status = 2;
							}else{
								self.status = 0;
							}
						}
					});
				}else{
					this.status = 2;
				}
			},
			selnav(e){
				this.comlist = [];
				this.isLoading = false;
				this.scroll = false;
				this.status = 0;
				this.pages = 1;
				this.page = 0;
				this.index = e;
				this.loadHistory();
			},
			
		}
		
	}
